import React from 'react';
import { connect } from 'react-redux';

import { IStoreState } from 'store/state';

interface IProps {
  rank: number;
}

const RatingResultContainer = ({
  rank,
}: IProps) => {
  if (rank === 1) {
    return (
      <>
        <div className="imgWrap">
          <img src={require('assets/img/haekbuldark.png')} alt=""/>
        </div>
        <div className="textWrap">
          <p className="title">
             <br/>
          <span className="bold">{rank}</span>.
          </p>
          <p className="subText">    </p>
        </div>
      </>
    )
  }
  
  if (rank === 2) {
    return (
      <>
        <div className="imgWrap">
          <img src={require('assets/img/yeobddeok.png')} alt=""/>
        </div>
        <div className="textWrap">
          <p className="title">
             <br/>
          <span className="bold">{rank}</span>.
          </p>
          <p className="subText">   </p>
        </div>
      </>
    )
  }

  if (rank === 3) {
    return (
      <>
        <div className="imgWrap">
          <img src={require('assets/img/buldark.png')} alt=""/>
        </div>
        <div className="textWrap">
          <p className="title">
             <br/>
          <span className="bold">{rank}</span>.
          </p>
          <p className="subText">     </p>
        </div>
      </>
    )
  }

  if (rank === 4) {
    return (
      <>
        <div className="imgWrap">
          <img src={require('assets/img/sinramyeon.png')} alt=""/>
        </div>
        <div className="textWrap">
          <p className="title">
             <br/>
          <span className="bold">{rank}</span>.
          </p>
          <p className="subText">  </p>
        </div>
      </>
    )
  }

  if (rank === 5) {
    return (
      <>
        <div className="imgWrap">
          <img src={require('assets/img/jinramyeon.png')} alt=""/>
        </div>
        <div className="textWrap">
          <p className="title">
             <br/>
          <span className="bold">{rank}</span>.
          </p>
          <p className="subText"> </p>
        </div>
      </>
    )
  }

  return null;
}

export default connect(
  (state: IStoreState) => ({
    rank: state.SurveyReducer.get('rank'),
  }),
)(RatingResultContainer);
